@import url(https://fonts.googleapis.com/css2?family=Convergence&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  font-family: "Convergence", sans-serif;
}

:root {
  --blue: #4a86e8;
  --light-blue: #c9daf8;
  --dark-blue: #0000ff;
  --hover-blue: #c9ddff;
  --border-black: #434343;
  --electronics: #ed6e85;
  --jewelery: #f2a254;
  --men-clothing: #f8cd6b;
  --women-clothing: #6cbdbf;
}

::selection {
  background: #0000ff;
  background: var(--dark-blue);
  color: white;
}

.App_app__3JMFR {
  height: 100vh;
  width: 100vw;
  position: relative;
}

.App_nav__joSl0 {
  height: 10%;
  width: 100%;
}

.App_body__1xjKC {
  height: 90%;
  width: 100%;
}

.App_chartModel__2p5rI {
  height: 90vh;
  width: 40vw;
  background: white;
  position: absolute;
  z-index: 100;
  right: 0px;
  top: 10%;
}

.App_app__3JMFR > button {
  padding: 0.4rem 1rem;
  font-size: 1rem;
  background: #0000ff;
  background: var(--dark-blue);
  border: 2px solid #434343;
  border: 2px solid var(--border-black);
  color: white;
  text-transform: uppercase;
  cursor: pointer;
  position: absolute;
  z-index: 10;
  right: 4rem;
  bottom: 5rem;
  transition: 0.1s ease-in-out;
}

.App_app__3JMFR > button:hover {
  box-shadow: 3px 3px 10px #434343;
  box-shadow: 3px 3px 10px var(--border-black);
}

@media screen and (max-width: 850px) {
  .App_nav__joSl0 {
    height: 15%;
  }

  .App_body__1xjKC {
    height: 85%;
  }

  .App_chartModel__2p5rI {
    height: 85vh;
    width: 50vw;
    top: 15%;
  }
}

@media screen and (max-width: 480px) {
  .App_nav__joSl0 {
    height: 20%;
  }

  .App_body__1xjKC {
    height: 80%;
  }

  .App_chartModel__2p5rI {
    height: 80vh;
    top: 20%;
  }
}

