@import url("https://fonts.googleapis.com/css2?family=Convergence&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
  font-family: "Convergence", sans-serif;
}

:root {
  --blue: #4a86e8;
  --light-blue: #c9daf8;
  --dark-blue: #0000ff;
  --hover-blue: #c9ddff;
  --border-black: #434343;
  --electronics: #ed6e85;
  --jewelery: #f2a254;
  --men-clothing: #f8cd6b;
  --women-clothing: #6cbdbf;
}

::selection {
  background: var(--dark-blue);
  color: white;
}

.app {
  height: 100vh;
  width: 100vw;
  position: relative;
}

.nav {
  height: 10%;
  width: 100%;
}

.body {
  height: 90%;
  width: 100%;
}

.chartModel {
  height: 90vh;
  width: 40vw;
  background: white;
  position: absolute;
  z-index: 100;
  right: 0px;
  top: 10%;
}

.app > button {
  padding: 0.4rem 1rem;
  font-size: 1rem;
  background: var(--dark-blue);
  border: 2px solid var(--border-black);
  color: white;
  text-transform: uppercase;
  cursor: pointer;
  position: absolute;
  z-index: 10;
  right: 4rem;
  bottom: 5rem;
  transition: 0.1s ease-in-out;
}

.app > button:hover {
  box-shadow: 3px 3px 10px var(--border-black);
}

@media screen and (max-width: 850px) {
  .nav {
    height: 15%;
  }

  .body {
    height: 85%;
  }

  .chartModel {
    height: 85vh;
    width: 50vw;
    top: 15%;
  }
}

@media screen and (max-width: 480px) {
  .nav {
    height: 20%;
  }

  .body {
    height: 80%;
  }

  .chartModel {
    height: 80vh;
    top: 20%;
  }
}
